.article_item_loader{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.article_item_container{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 500px;
    width: 400px;
    margin: 25px 40px;
    border-radius: 15px;
    overflow: hidden;
    background-color: rgb(34, 37, 46);
    cursor: pointer;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.747);
    transition: 0.4s;

    &:hover{
        transform: scale(1.1);
    }

    @media(max-width: 900px){
        height: auto;
        width: 100vw;
        margin: 25px 0px;
        padding-bottom: 30px;
    }
}
.article_item_img{
    min-width: 400px;
    height: 260px;
    border-bottom: 2px solid rgb(255, 255, 255);

    @media(max-width: 900px){
        min-width: 0;
        height: 50vw;
        width: 100%;
    }

    @media(max-width: 600px){
        height: 48vw;
    }

    @media(max-width: 400px){
        height: 45vw;
    }
}
.article_item_title{
    text-align: center;
    font-size: 30px;
    margin-top: 35px;
    margin-bottom: 50px;
    text-transform: uppercase;
    box-sizing: border-box;
    padding: 0 25px;
    letter-spacing: 1px;

    @media(max-width: 900px){
        font-size: 23px;
        margin: 25px 0;
        padding: 0 15px;
    }
}
.article_item_thumbtext{
    text-align: center;
    box-sizing: border-box;
    padding: 0 15px;
    width: 100%;

    @media(max-width: 900px){
        font-size: 13px;
        line-height: 15px;
    }
}